.playbar {
	/* position: fixed; */
	background-color: #1c508c;	
  
	/* width: calc(100vw); */
	
  
	left: 0;
	bottom: 0;
  
	display: flex;
	align-items: center;
	padding: 10px 20px;
	gap: 30px;
	/* border-radius: 0 0 9px 9px; */
}

.playbar p {
	font-size: 14px;
}
	/* & > .togglePlay {
	  transform: scale(120%) !important;
	}
  
	& * {
	  color: white !important;
	} */

  
.preview {
	width: 55px;
	height: 55px;
	border-radius: 10px;
}
  
.credits {
	color: white;
	display: flex;
	flex-direction: column;
	gap: 4px;
	letter-spacing: 2px;
} 

.credits p {
	  font-size: 14px;
}

  
.slider {
	display: flex;
	align-items: center;
	gap: 20px;
	flex-basis: 600px;
	margin-left: auto;
	color: white;
}

.slider p {
	min-width: 50px;
}