.loading {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 40px;
    display: flex;
    align-items: center;
}
.obj {
    width: 15px;
    height: 100px;
    background: #fff;
    margin: 0 9px;
    border-radius: 15px;
    animation: loading 0.8s infinite;
}
.obj:nth-child(2) {
    animation-delay: 0.1s;
}
.obj:nth-child(3) {
    animation-delay: 0.2s;
}
.obj:nth-child(4) {
    animation-delay: 0.3s;
}
.obj:nth-child(5) {
    animation-delay: 0.4s;
}
.obj:nth-child(6) {
    animation-delay: 0.5s;
}
.obj:nth-child(7) {
    animation-delay: 0.6s;
}
.obj:nth-child(8) {
    animation-delay: 0.7s;
}
@keyframes loading {
    0% {
        height: 0;
    }
    50% {
        height: 100px;
    }
    100% {
        height: 0;
    }
}
