@font-face {
    font-family: 'Rubik';
    font-weight: 300;
    font-style: normal;
    src: url('./assets/font/Rubik-Light.woff2') format('woff2'), url('./assets/font/Rubik-Light.woff') format('woff');
    font-display: swap;
}

@font-face {
    font-family: 'Rubik';
    font-weight: 400;
    font-style: normal;
    src: url('./assets/font/Rubik-Regular.woff2') format('woff2'),
        url('./assets/font/Rubik-Regular.woff') format('woff');
    font-display: swap;
}

@font-face {
    font-family: 'Rubik';
    font-weight: 500;
    font-style: normal;
    src: url('./assets/font/Rubik-Medium.woff2') format('woff2'), url('./assets/font/Rubik-Medium.woff') format('woff');
    font-display: swap;
}

@font-face {
    font-family: 'Rubik';
    font-weight: 700;
    font-style: normal;
    src: url('./assets/font/Rubik-Bold.woff2') format('woff2'), url('./assets/font/Rubik-Bold.woff') format('woff');
    font-display: swap;
}

body {
    font-family: 'Rubik', sans-serif;
    font-weight: 400;
    font-style: normal;
    font-size: 16px;
    line-height: 1.5;
    background-color: #e9e8e4;
}

html {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}

*,
*::after,
*::before {
    -webkit-box-sizing: inherit;
    box-sizing: inherit;
}

ul,
ol {
    padding: 0;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
    margin: 0;
}

ul[class] {
    list-style: none;
}

img {
    max-width: 100%;
    display: block;
}

input,
button,
textarea,
select {
    font: inherit;
}

a {
    text-decoration: none;
    color: inherit;
}
